@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("https://images.unsplash.com/photo-1488818138649-d2734488e6d0?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=967&q=80");
  background-size: cover;
  background-position: center center;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-top: 2rem;
  font-weight: normal;
  font-size: 2.5rem;
}

.container-countdown {
  display: flex;
  margin-top: 1.2rem;
}

.value-element {
  margin: 0 2rem;
  text-align: center;
}

.value-element span {
  font-size: 1.5rem;
}

.big-text {
  font-size: 3rem;
  font-weight: bold;
  line-height: 1;
}
